import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import gen from '../assets/ejes/genn.jpg';
import obr from '../assets/ejes/obr.png';
import ecoAm from '../assets/ejes/maff.png';
import noi from '../assets/noimg.jpg';
import colores from '../styles/colors';
import port from '../assets/portyt.png'
import Avatar from '@material-ui/core/Avatar';
import jorA from '../assets/fondo/jora2.jpg'
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import BusinessIcon from '@material-ui/icons/Business';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';

const ProyectosModal = ({ open, handleClose }) => {
    const classes = useStyles();

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar style={{ backgroundColor: colores.mainLightfull }}>
                        <IconButton
                            edge='start'
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label='close'
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant='h6' className={classes.title}>
                            Proyectos Presentados en HCSF
                        </Typography>
                    </Toolbar>
                </AppBar>
                <div style={{ padding: 90, paddingTop: 40, backgroundColor: colores.fondoLight }}>
                    <ContentEjes />
                </div>
            </Dialog>
        </div>
    );
};

const ContentEjes = () => {
    return (<div>
        <Grid container spacing={3} justify="center" >
        <Grid  item xs={3}>
                <div style={{borderRadius: 15}}>
                   <img src={jorA} alt="jorr" style={{width: '100%', height: null, borderRadius: 15}} />
                </div>
        </Grid>
        <Grid  item xs={8} >
            <Grid container spacing={3} >
                {dataDash.map((dataCard, i) => (
                    <Grow direction="up" in={true} mountOnEnter  timeout={600 * i + 600}>
                     <Grid item xs={6} >
                     <CardDash {...dataCard} />
                     </Grid>
                     </Grow>
                ))}
                 <Grid item xs={6} >
        
            </Grid>
              
            </Grid>
        </Grid>
        
                {proyectos.map((proyect, i) => {

                    return  <Grow direction="up" in={true} mountOnEnter  timeout={600 * i + 600}><Grid item xs={6}> <CardProyect {...proyect} /></Grid></Grow> 
                })}
          

        </Grid>
        </div>);
};

const dataDash = [
    {title: 'Proyectos Presentados', total: 122, icon: <AccountBalanceIcon fontSize="large"  />, color: colores.cardGradiant1 },
    {title: 'Proyectos Aprobados', total: 87, icon: <BeenhereIcon fontSize="large" />, color: colores.cardGradiant3 },
    {title: 'Sesiones Realizadas', total: 131, icon: <BusinessIcon fontSize="large" />, color: colores.cardGradiant2 },
    {title: 'Conoce Mis Proyectos', total: 0, icon: <ArrowDownwardIcon fontSize="large"/>, color: colores.cardGradiant4}
]


const CardDash = ({title, total, icon, color}) => {

    if(total === 0){

        return (<Card elevation={3} style={{width: '100%',minHeight: 222, backgroundColor: color,display:'flex', flexDirection: 'column-reverse'}}>
        <CardContent style={{textAlign: 'center', alignContent: 'space-between'}}>
              
        <Typography style={{fontSize: 26, }} >{title}</Typography>      
        <Fab style={{height: 55, width: 55, marginBottom: 15, pointerEvents: 'none', marginTop: 30}} > {icon}</Fab>   
       
        </CardContent>
         </Card>)
    }


    return <Card elevation={3} style={{width: '100%', backgroundColor: color}}>
        <CardContent style={{textAlign: 'center'}}>
        <Fab style={{height: 50, width: 50, marginBottom: 15, pointerEvents: 'none'}} > {icon}</Fab>   
        
        <Typography style={{fontSize: 55}} >{total}</Typography>          
        <Typography style={{fontSize: 26}} >{title}</Typography>      
        </CardContent>
    </Card>
}

const CardProyect = ({ name, text, img }) => {
    return (
        <Card elevation={3} style={{height: 300,display: 'flex'} }><CardMedia><img src={img} alt={name} style={{width: '70%', height: '100%'}} />
            </CardMedia>
            <CardContent>
            <Typography gutterBottom variant='h5' component='h2'>
            {name}
            </Typography>
            <Typography gutterBottom variant='body2' component='body2'>
                {text}
            </Typography>
            </CardContent>
            </Card>
    );
};

const proyectos = [
{ name: 'proyecto Uno', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
 { name: 'proyecto Dos', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi }, 
 { name: 'proyecto Tres', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Cuatro', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Cinco', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Seis', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
  { name: 'proyecto Siete', text: 'lorem ipsu set amet lorian gagot fete lorem ipsu set amente larot fen deter', img: noi },
]

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
    appBar: {
        position: 'relative',
    },

    title: {
        marginLeft: 5,
        color: colores.mainDarkfull,
    },
}));

export default ProyectosModal;
